:root {
  /* 5-color palette (exactly) */
  --bg: #eaf1ff;           /* soft pastel blue background */
  --surface: #f6f8ff;      /* elevated surfaces */
  --text: #2d3348;         /* primary text (dark slate) */
  --primary: #6e85ff;      /* blue accent for actions */
  --accent: #ff8db1;       /* soft pink accent */

  /* neumorphic shadows derived from background tone */
  --shadow-dark: #ccd6ec;
  --shadow-light: #ffffff;
  --radius: 18px;
}

/* Reset + base */
* { box-sizing: border-box; }

body {
  margin: 0;
  background: var(--bg);
  color: var(--text);
  font-family: ui-sans-serif, system-ui, -apple-system, Segoe UI, Roboto, Inter, "Helvetica Neue", Arial, "Apple Color Emoji","Segoe UI Emoji";
  line-height: 1.55;
}

img { max-width: 100%; display: block; }
a { color: inherit; text-decoration: none; }

.container { max-width: 1200px; margin: 0 auto; padding: 20px; }

/* Neumorphism helpers */
.chip {
  padding: 8px 14px; 
  border-radius: 999px;
  background: var(--surface);
  box-shadow: 6px 6px 14px var(--shadow-dark), -6px -6px 14px var(--shadow-light);
  transition: background .2s ease, color .2s ease, box-shadow .2s ease;
  cursor: pointer; 
  white-space: nowrap;
  display: inline-block;
}

.chip[data-active="true"] {
  background: linear-gradient(145deg, var(--primary), #7e97ff);
  color: #fff;
  box-shadow: 8px 8px 18px rgba(110,133,255,.35), -8px -8px 18px #fff;
}

/* Navigation */
header.nav {
  position: sticky; 
  top: 0; 
  z-index: 5;
  background: var(--surface);
  border-bottom: 1px solid color-mix(in srgb, var(--text) 15%, transparent);
  box-shadow: 0 4px 12px var(--shadow-dark);
}

.nav-inner { 
  display: flex;
  align-items: center;
  justify-content: space-between; 
  gap: 20px; 
  padding: 14px 0;
}

header.nav .container {
  padding: 0 32px 0 16px;
  margin: 0;
  max-width: 100%;
  width: 100%;
}

.brand {
  flex-shrink: 0;
  display: flex;
  align-items: center;
  margin-right: 16px;
  padding-left: 0;
}

.brand img {
  display: block;
}

.nav-links {
  display: flex;
  align-items: center;
  gap: 24px;
  font-size: 2rem;
  flex-shrink: 0;
  margin-left: auto;
}

.nav-links a,
.nav-links button {
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--primary);
  transition: opacity .2s ease;
}

.nav-links a:hover,
.nav-links button:hover {
  opacity: .85;
}

.nav-links button {
  background: transparent;
  border: 0;
  padding: 0;
  cursor: pointer;
  font-size: inherit;
}

.nav-links button:focus-visible {
  outline: 2px solid var(--primary);
  outline-offset: 4px;
}

.nav-links a:focus {
  outline: none;
}

.nav-links a:focus-visible {
  outline: 2px solid var(--primary);
  outline-offset: 4px;
}

.nav-links i {
  color: inherit;
}

/* Search with filters */
.search-wrap { 
  flex: 1 1 700px;
  display: flex;
  gap: 20px;
  align-items: center; 
  justify-content: center; 
}

.search { 
  width: 100px;
  flex: 1;
  display: flex; 
  align-items: center;
  gap: 10px; 
  padding: 10px 16px; 
  border-radius: 999px; 
  background: var(--surface);
  box-shadow: inset 6px 6px 12px var(--shadow-dark);
}

.search input {
  flex: 1; 
  border: 0; 
  outline: 0; 
  background: transparent; 
  color: var(--text); 
  font-size: 14px;
}

.filters { 
  display: flex; 
  gap: 18px; 
  overflow: auto; 
}

.filters a {
  color: inherit;
}

.filter-group { 
  display: flex; 
  gap: 8px; 
}

.filter-group input { 
  display: none; 
}

.filter-group label { 
  user-select: none; 
}

.filter-group input:checked + label { 
  background: linear-gradient(145deg, var(--primary), #7e97ff); 
  color: #fff; 
}
